.Arrows {
  margin-bottom: 2rem;
  display: flex;
  flex-direction: row;
  column-gap: var(--arrow-column-gap);
  opacity: 0.7;
}

.Arrows > .button {
  position: relative;
  flex-grow: 1;
}

.Arrows > .button.disabled,
.Arrows > .button.pressed {
  opacity: 0.2;
}

.Arrows > .button > .img-container {
  position: relative;
  width: 0;
  height: var(--arrow-img-height);
  border-bottom: 1px solid black;
  font-size: calc(0.7 * var(--font-size-author));
  transition-property: width;
  transition-duration: 1s;
}

.Arrows > .button > .img-container.loaded {
  width: 100%;
}

.Arrows > .button:not(.disabled) > .img-container {
  cursor: pointer;
}

.Arrows > .button.forward > .img-container {
  position: absolute;
  right: 0;
}

.Arrows > .button > .img-container > img {
  position: absolute;
  height: var(--arrow-img-height);
  bottom: var(--arrow-img-top);
}

.Arrows > .button.back > .img-container > img {
  transform: rotateY(180deg);
}

.Arrows > .button.back > .img-container > img {
  right: 0;
}

.Arrows > .button.forward > .img-container > img {
  left: 0;
}

.Arrows > .button > .img-container > span {
  position: relative;
  top: 1rem;
}

/* || Reduced motion */

@media (prefers-reduced-motion) {
  .Arrows > .button > .img-container {
    transition-duration: 0s;
  }
}
