.App {
  position: relative;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: var(--color-background);
}

.App > .content {
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  transition-property: left;
  transition-duration: 1s;
}

.App > .content.hidden {
  left: 100%;
}

.App > .toggle {
  position: fixed;
  top: 0;
  right: 0;
  padding: 1rem;
  padding-top: 0.3rem;
  padding-right: 0.8rem;
  font-family: sans-serif;
  font-size: var(--font-size-author);
  font-weight: 100;
  cursor: pointer;
}

/* || Reduced motion */

@media (prefers-reduced-motion) {
  .App > .content {
    transition-duration: 0s;
  }

  .App > .content.hidden {
    display: none;
    left: 0;
  }
}

/* || Variables */

.App {
  --width-max: 600px;
  --font-size-saying: 2.9rem;
  --font-size-author: 1.35rem;
  --color-foreground: black;
  --color-background: white;
  --arrow-column-gap: 6rem;
  --arrow-img-height: 2.5rem;
  --arrow-img-top: -0.5rem;
}

@media (min-width: 550px) {
  .App {
    --font-size-saying: 4rem;
    --font-size-author: 1.5rem;
    --arrow-img-height: 3.5rem;
    --arrow-img-top: -0.7rem;
  }
}

@media (max-height: 350px) {
  .App {
    --width-max: 400px;
    --font-size-saying: 2rem;
    --font-size-author: 1rem;
    --arrow-img-height: 1.5rem;
    --arrow-img-top: -0.3rem;
  }
}
